import useScrollToTop from '../hooks/useScrollToTop';
import './styles/ChinhSach.scss';

const ChinhSach = () => {
  useScrollToTop();
  return (
    <div className="ChinhSach page-layout flash">
      <div>
        <header>
          <br />
          <br />
        </header>
        <div className="col-12 mb-2 text-center">
          <img
            className
            alt="140x140"
            style={{ width: '61%' }}
            src="https://localhost:44307/Image/Home/1.png"
            data-holder-rendered="true"
          />
        </div>
        <br />
        <section>
          <div
            className="container"
            style={{ backgroundColor: '#fce0d3', borderRadius: 10 }}
          >
            <div className="col-lg-12 col-md-7 col-sm-12">
              <br />
              <p
                className="text-center"
                style={{ fontSize: 25, fontWeight: 600 }}
              >
                Thông báo
              </p>
              <p style={{ fontSize: 15, fontWeight: 400 }}>
                <a style={{ fontWeight: 'bold', fontSize: 17 }}>ADDDA</a> xin
                thông báo về việc bổ sung{' '}
                <a style={{ fontWeight: 'bold', fontSize: 17 }}>
                  Chính sách bảo mật
                </a>{' '}
                liên quan đến các vấn đề mới trong việc bảo vệ dữ liệu cá nhân
                theo Nghị định 13/2023/NĐ-CP của Chính phủ Việt Nam.
                <br />
                <br /> Trong quá trình thiết lập mối quan hệ giữa{' '}
                <a style={{ fontWeight: 'bold', fontSize: 17 }}>ADDDA</a> và
                Người dùng, giữ các người dùng với nhau phụ thuộc vào từng loại
                hình dịch vụ mà chúng tôi cung cấp, ADDDA có thể thu thập và xử
                lý dữ liệu cá nhân của Quý Khách hàng.{' '}
                <a style={{ fontWeight: 'bold', fontSize: 17 }}>ADDDA</a> cam
                kết đảm bảo an toàn và Bảo vệ dữ liệu cá nhân của Quý người dùng
                theo quy định của pháp luật Việt Nam. <br />
                <br /> Theo đó bắt đầu từ ngày ra thông báo này, chúng tôi cần
                xác nhận lại sự đồng ý của bạn để tiếp tục thu thập, xử lý và
                chia sẻ dữ liệu cá nhân của bạn. Tuy nhiên, chúng tôi muốn nhắc
                nhở rằng nếu thu hồi sự đồng ý của mình, Quý Người dùng sẽ không
                thể tiếp cận với những người dùng khác trên nền tảng để phục vụ
                nhu cầu sử dụng dịch vụ của mình.
                <br />
                <br /> <a style={{ fontWeight: 'bold', fontSize: 17 }}>
                  ADDDA
                </a>{' '}
                hiểu rằng việc bảo vệ dữ liệu cá nhân là rất quan trọng, và
                chúng tôi cam kết tuân thủ Nghị định 13/2023/NĐ-CP và các quy
                định về bảo vệ dữ liệu liên quan khác. Bỏ qua thông tin này nếu
                bạn đồng ý để chia sẻ thông tin cá nhân của mình với các Người
                dùng khác trên nền tảng{' '}
                <a style={{ fontWeight: 'bold', fontSize: 17 }}>ADDDA</a>. Hoặc
                vào tài khoản của mình vào để thu hồi/xóa dữ liệu. Cảm ơn sự
                quan tâm của bạn về vấn đề này. Chúng tôi rất trân trọng và hy
                vọng sẽ có cơ hội tiếp tục hỗ trợ bạn trong tương lai.
              </p>
              <br />
            </div>
          </div>
          <br />
          <br />
        </section>
        {/*--------------------------------------------------------*/}
        <section>
          <div className="container ">
            <div className="row">
              <div className="col-lg-12 col-md-7 col-sm-12 text-center">
                <h2>Chính sách bảo mật</h2>
              </div>
              <div className="col-lg-12 col-md-7 col-sm-12">
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  1. Giới thiệu
                </p>
                <p>
                  - Chào mừng bạn đến với nền tảng ADDDA.vn (bao gồm website và
                  ứng dụng di động) được được vận hành bởi Công ty Cổ Phần ADDDA
                  Asia. ADDDA thông báo đến bạn và cần sự đồng ý và cho phép của
                  bạn để xử lý, thu thập và lưu trữ dữ liệu của bạn nhằm mục
                  đích cung cấp các dịch vụ cho bạn trong suốt quá trình sử dụng
                  Website và Ứng dụng di động, chúng tôi cam kết nghiêm túc thực
                  hiện trách nhiệm của mình liên quan đến bảo mật thông tin cá
                  nhân và tôn trọng quyền riêng tư của tất cả người dùng trên
                  nền tảng.
                  <br />
                  <br />
                  - ADDDA là một sàn giao dịch thương mại điện tử (“Sàn TMĐT) đã
                  được đăng ký với Bộ Công Thương. Người dùng trên sàn bao gồm
                  người bán (chủ xe), người mua (khách thuê) và các bên khác sử
                  dụng dịch vụ của chúng tôi.
                  <br />
                  <br />
                  - "Dữ Liệu Cá Nhân" có nghĩa là dữ liệu, dù đúng hay không, về
                  một cá nhân mà thông qua đó có thể xác định được danh tính,
                  hoặc từ dữ liệu đó và thông tin khác mà một tổ chức có hoặc có
                  khả năng tiếp cận. Các ví dụ thường gặp về dữ liệu cá nhân có
                  thể gồm có tên, số chứng minh nhân dân/căn cước công dân, mã
                  số thuế, giấy phép lái xe và thông tin liên hệ.
                  <br />
                  <br />
                  - Chính sách bảo mật này ("Chính sách bảo mật" hay "Chính
                  sách") được thiết kế để giúp bạn hiểu được cách thức chúng tôi
                  thu thập, sử dụng, tiết lộ và/hoặc xử lý dữ liệu cá nhân mà
                  bạn đã cung cấp cho chúng tôi và/hoặc lưu giữ về bạn, cho dù
                  là hiện nay hoặc trong tương lai, cũng như để giúp bạn đưa ra
                  quyết định sáng suốt trước khi cung cấp cho chúng tôi bất kỳ
                  dữ liệu cá nhân nào của bạn.
                  <br />
                  <br />
                  - Bằng việc sử dụng Các Dịch Vụ, đăng ký tài khoản với chúng
                  tôi hoặc truy cập Nền tảng, bạn xác nhận và đồng ý rằng bạn
                  chấp nhận các phương pháp, yêu cầu, và/hoặc chính sách được mô
                  tả trong Chính sách bảo mật này, và theo đây bạn đồng ý cho
                  phép chúng tôi thu thập, sử dụng, tiết lộ và/hoặc xử lý dữ
                  liệu cá nhân của bạn như mô tả trong đây. Việc cho phép này sẽ
                  có hiệu lực và bắt đầu từ thời điểm bạn đồng ý cho đến khi bạn
                  quyết định rút lại sự đồng ý hoặc tự động kết thúc trong các
                  trường hợp theo quy định của pháp luật.
                  <br />
                  <br />
                  - Nếu bạn không đồng ý cho phép xử lý dữ liệu của bạn theo quy
                  định này, vui lòng không sử dụng các dịch vụ của chúng tôi
                  hoặc truy cập nền tảng của chúng tôi hoặc thông báo ngay cho
                  chúng tôi về việc không đồng ý của bạn trong trương hợp bạn đã
                  đồng ý trước đó.
                  <br />
                  <br />
                  - Nếu có bất kỳ thay đổi nào về Chính sách bảo mật của mình,
                  chúng tôi sẽ thông báo cho bạn bao gồm cả thông qua việc đăng
                  tải những thay đổi đó hoặc Chính sách bảo mật sửa đổi trên Nền
                  tảng của chúng tôi. Trong phạm vi pháp luật cho phép, việc
                  tiếp tục sử dụng các Dịch Vụ hoặc Nền Tảng, bao gồm giao dịch
                  của bạn, được xem là bạn đã công nhận và đồng ý với các thay
                  đổi trong Chính Sách Bảo Mật này.
                  <br />
                  <br />
                  - Chính sách này áp dụng cùng với các thông báo, điều khoản
                  hợp đồng, điều khoản chấp thuận khác áp dụng liên quan đến
                  việc chúng tôi thu thập, lưu trữ, sử dụng, tiết lộ và/hoặc xử
                  lý dữ liệu cá nhân của bạn và không nhằm ghi đè những thông
                  báo hoặc các điều khoản đó trừ khi chúng tôi có tuyên bố ràng
                  khác.
                  <br />
                  <br />
                  - Chính sách này được áp dụng cho tất cả người dùng đang sử
                  dụng Dịch vụ/Nền tảng trừ khi có tuyên bố rõ ràng ngược lại.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  2. Khi nào ADDDA sẽ thu thập dữ liệu cá nhân?
                </p>
                <p>
                  - Chúng tôi sẽ/có thể thu thập dữ liệu cá nhân về bạn: <br />
                  <br />
                  * Khi bạn đăng ký và/hoặc sử dụng Các Dịch Vụ hoặc Nền tảng
                  của chúng tôi;
                  <br />
                  <br />
                  * Khi bạn gửi bất kỳ biểu mẫu nào, bao gồm đơn đăng ký hoặc
                  các mẫu đơn khác liên quan đến bất kỳ sản phẩm và dịch vụ nào
                  của chúng tôi, bằng hình thức trực tuyến hay dưới hình thức
                  khác;
                  <br />
                  <br />
                  * Khi bạn ký kết bất kỳ thỏa thuận nào hoặc cung cấp các tài
                  liệu hoặc thông tin khác liên quan đến tương tác giữa bạn với
                  chúng tôi/khách hàng của chúng tôi, hoặc khi bạn sử dụng các
                  sản phẩm và dịch vụ của chúng tôi;
                  <br />
                  <br />
                  * Khi bạn tương tác với chúng tôi, chẳng hạn như thông qua các
                  cuộc gọi điện thoại (có thể được ghi âm lại), thư từ, fax, gặp
                  gỡ trực tiếp, các nền ứng dụng truyền thông xã hội và email;
                  <br />
                  <br />
                  * Khi bạn sử dụng các dịch vụ điện tử của chúng tôi, hoặc
                  tương tác với chúng tôi qua Nền tảng hoặc Trang Web hoặc Các
                  Dịch Vụ của chúng tôi. Trường hợp này bao gồm thông qua tập
                  tin cookie mà chúng tôi có thể triển khai khi bạn tương tác
                  với các Nền tảng hoặc Trang Web của chúng tôi;
                  <br />
                  <br />
                  * Khi bạn liên kết tài khoản ADDDA với tài khoản mạng xã hội
                  của bạn hoặc các tài khoản bên ngoài khác hoặc sử dụng các
                  tính năng mạng xã hội khác, phù hợp với các chính sách của nhà
                  cung cấp
                  <br />
                  <br />
                  * Khi bạn thực hiện các giao dịch thông qua Dịch vụ của chúng
                  tôi;
                  <br />
                  <br />
                  * Khi bạn cung cấp ý kiến phản hồi hoặc gửi khiếu nại cho
                  chúng tôi;
                  <br />
                  <br />
                  * Khi bạn đăng ký tham gia một cuộc thi; hoặc khi bạn gửi dữ
                  liệu cá nhân của bạn cho chúng tôi vì bất kỳ lý do gì.
                  <br />
                  <br />
                  - Các trường hợp trên không nhằm mục đích liệt kê đầy đủ các
                  trường hợp và chỉ đưa ra một số trường hợp phổ biến về thời
                  điểm dữ liệu cá nhân của bạn có thể bị thu thập.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  3. Phạm vi, mục đích thu thập và sử dụng thông tin
                </p>
                <p>
                  <a style={{ fontWeight: 'bold' }}>
                    * Phạm vi thu thập
                    <br />
                  </a>
                  -{' '}
                  <a style={{ fontWeight: 'bold' }}>
                    Đối với Người bán/chủ xe:
                  </a>{' '}
                  Họ tên, email, chứng minh nhân dân/căn cước công dân, mã số
                  thuế, điện thoại, tên đăng nhập, mật khẩu đăng nhập, địa chỉ
                  sinh sống, tài khoản ngân hàng và thông tin thanh toán, các
                  hình ảnh và thông tin về sản phẩm muốn đăng tải trên nền tảng.
                  <br />
                  <br />-{' '}
                  <a style={{ fontWeight: 'bold' }}>
                    Đối với Người mua/khách thuê:
                  </a>{' '}
                  Họ tên, email, chứng minh nhân dân/căn cước công dân, giấy
                  phép lái xe ô tô, điện thoại, tên đăng nhập, mật khẩu đăng
                  nhập, địa chỉ sinh sống, tài khoản ngân hàng và thông tin
                  thanh toán.
                  <br />
                  <br />
                  - Các thành viên sẽ tự chịu trách nhiệm về bảo mật và lưu giữ
                  mọi hoạt động sử dụng dịch vụ dưới tên đăng ký, mật khẩu và
                  hộp thư điện tử của mình. Ngoài ra, thành viên có trách nhiệm
                  thông báo kịp thời cho ADDDA về những hành vi sử dụng trái
                  phép, lạm dụng, vi phạm bảo mật, lưu giữ tên đăng ký và mật
                  khẩu của bên thứ ba để có biện pháp giải quyết phù hợp.
                  <br />
                  <br />
                  - Bạn đồng ý không cung cấp cho chúng tôi bất cứ thông tin nào
                  không chính xác hoặc gây hiểu nhầm và bạn đồng ý sẽ thông báo
                  cho chúng tôi về bất cứ thông tin nào không chính xác hoặc khi
                  có sự thay đổi thông tin.
                  <br />
                  <br />
                  - Nếu bạn đăng nhập để trở thành người dùng các Nền tảng của
                  chúng tôi sử dụng tài khoản mạng xã hội của Bạn (“Tài khoản
                  Mạng Xã hội”), liên kết tài khoản của bạn với Tài khoản Mạng
                  Xã hội của bạn hoặc sử dụng bất cứ tính năng mạng xã hội nào,
                  chúng tôi có quyền truy cập thông tin về bạn mà bạn đã cung
                  cấp một cách tự nguyên cho nhà cung cấp dịch vụ Tài khoản Mạng
                  Xã hội của bạn và tuân theo các chính sách của các nhà cung
                  cấp dịch vụ này, và chúng tôi sẽ quản lý và sử dụng các dữ
                  liệu cá nhân này của bạn theo các quy định của Chính sách này
                  tại mọi thời điểm.
                  <br />
                  <br />
                  <a style={{ fontWeight: 'bold' }}>
                    * Mục đích thu thập và sử dụng thông tin
                    <br />
                  </a>
                  - ADDDA thu thập thông tin thành viên để:
                  <br />
                  <br />
                  - Xác minh danh tính của bạn và để quản lý tài khoản trực
                  tuyến mà bạn có thể đã thiết lập với chúng tôi;
                  <br />
                  - Liên hệ xác nhận khi khách hàng đăng ký sử dụng dịch vụ trên
                  website nhằm đảm bảo quyền lợi cho cho người tiêu dùng;
                  <br />
                  - Hỗ trợ việc đặt xe và cung cấp xe cho Khách hàng;
                  <br />
                  - Cung cấp các dịch vụ đến thành viên;
                  <br />
                  - Để xử lý các đơn đặt hàng bạn gửi qua Nền tảng;
                  <br />
                  - Gửi các thông báo về các hoạt động trao đổi thông tin giữa
                  thành viên và ADDDA bao gồm nhưng không giới hạn: chương trình
                  khuyến mại, khảo sát ý kiến, tiếp thị và quảng cáo sản phẩm;
                  <br />
                  - Liên lạc với bạn để xử lý và trả lời các truy vấn, phản hồi,
                  khiếu nại hoặc tranh chấp của bạn, cho dù trực tiếp hoặc thông
                  qua bất kỳ nhà cung cấp dịch vụ khách hàng được thuê ở bên
                  ngoài nào;
                  <br />
                  - Để lập số liệu thống kê và nghiên cứu đáp ứng yêu cầu báo
                  cáo và/hoặc duy trì sổ sách nội bộ hoặc theo quy định;
                  <br />
                  - Để thực hiện quy trình tìm hiểu và xác minh hoặc các hoạt
                  động sàng lọc khác (bao gồm nhưng không giới hạn kiểm tra lý
                  lịch) tuân thủ các nghĩa vụ theo quy định pháp luật hoặc cơ
                  quan nhà nước có thẩm quyền hoặc các thủ tục kiểm soát rủi ro
                  của chúng tôi, có thể được pháp luật yêu cầu hoặc có thể đã
                  được chúng tôi áp dụng;
                  <br />
                  - Ngăn ngừa các hoạt động phá hủy tài khoản người dùng của
                  thành viên hoặc các hoạt động giả mạo thành viên;
                  <br />
                  - Để ngăn chặn hoặc điều tra bất kỳ hoạt động gian lận thực tế
                  hoặc bị nghi ngờ nào đối với Điều khoản dịch vụ của chúng tôi,
                  gian lận, các hành vi phi pháp, thiếu sót hay hành vi sai trái
                  nào, cho dù có liên quan đến việc bạn sử dụng Các Dịch Vụ của
                  chúng tôi hay không hay bất kỳ vấn đề nào phát sinh từ quan hệ
                  của bạn với chúng tôi; Phạm vi sử dụng thông tin
                  <br />
                  - Để bảo vệ quyền lợi của công ty và những đối tác của công
                  ty: Chúng tôi chỉ đưa ra những thông tin cá nhân của khách
                  hàng khi chắc chắn rằng những thông tin đó có thể bảo vệ được
                  quyền lợi, tài sản của công ty chúng tôi và những đối tác liên
                  quan. Những thông tin này sẽ được tiết lộ một cách hợp pháp
                  theo Pháp luật Việt Nam.
                  <br />
                  - Trong trường hợp có yêu cầu của pháp luật: ADDDA có trách
                  nhiệm hợp tác cung cấp thông tin cá nhân thành viên khi có yêu
                  cầu từ cơ quan tư pháp bao gồm: Viện kiểm sát, tòa án, cơ quan
                  thuế, cơ quan công an điều tra liên quan đến hành vi vi phạm
                  pháp luật nào đó của thành viên. Ngoài ra, không ai có quyền
                  xâm phạm vào thông tin cá nhân của thành viên.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  4. Những người hoặc tổ chức có thể được tiếp cận với thông tin
                </p>
                <p>
                  - ADDDA sẽ không tiết lộ thông tin của khách hàng cho các bên
                  thứ ba vì mục đích kinh doanh hoặc tiếp thị độc lập của riêng
                  họ mà không có sự đồng ý của bạn. Tuy nhiên, ADDDA có thể tiết
                  lộ thông tin của khách hàng cho các đối tượng sau:
                  <br />
                  <br />
                  - Các cơ quan nhà nước có thẩm quyền: ADDDA sẽ cung cấp thông
                  tin cá nhân của khách hàng theo yêu cầu của các cơ quan này.
                  <br />
                  - Ban quản trị ADDDA, Bộ phận kinh doanh, Bộ phận Chăm sóc
                  khách hàng và các cá nhân được phân công thực hiện các công
                  việc có liên quan: Việc tiếp cận thông tin của các đối tượng
                  này nhằm cung cấp dịch vụ cũng như hỗ trợ khách hàng trong quá
                  trình sử dụng các dịch vụ được cung cấp bởi ADDDA.
                  <br />
                  - Các bên khác khi có sự đồng ý hoặc theo hướng dẫn của khách
                  hàng: Ngoài các tiết lộ như được mô tả trong Chính sách bảo
                  mật thông tin cá nhân này, ADDDA có thể chia sẻ thông tin cá
                  nhân của khách hàng với các bên thứ ba khi khách hàng đồng ý
                  hoặc yêu cầu việc chia sẻ như vậy.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  5. ADDDA có tiết lộ thông tin của người dùng không?
                </p>
                <p>
                  - Trong quá trình thực hiện hoạt động kinh doanh, chúng tôi
                  sẽ/có thể cần phải sử dụng, xử lý, tiết lộ và/hoặc chuyển giao
                  dữ liệu cá nhân của bạn cho bên thứ ba bao gồm nhưng không
                  giới hạn: cơ quan nhà nước có thẩm quyền, các công ty liên kết
                  hoặc liên quan của chúng tôi,… việc tiết lộ này sẽ được thực
                  hiện theo đúng trình tự và quy định của pháp luật hiện hành.
                  Chúng tôi cố gắng đảm bảo rằng các bên thứ ba này giữ an toàn
                  cho dữ liệu cá nhân của bạn khỏi bị truy cập, thu thập, sử
                  dụng, tiết lộ, xử lý trái phép hoặc các rủi ro tương tự và chỉ
                  lưu giữ dữ liệu cá nhân của bạn miễn là dữ liệu cá nhân của
                  bạn vẫn còn cần thiết cho những việc nêu trên.
                  <br />
                  <br />
                  - Để tránh nghi ngờ, trong trường hợp các quy định của pháp
                  luật về bảo vệ bí mật thông tin cá nhân hoặc các điều luật
                  hiện hành khác cho phép một tổ chức chẳng hạn như chúng tôi
                  thu thập, sử dụng hoặc tiết lộ dữ liệu cá nhân của bạn mà
                  không cần sự đồng ý của bạn, sự cho phép như thế của pháp luật
                  sẽ tiếp tục áp dụng. Phù hợp với các quy định nêu trên và theo
                  các quy định của pháp luật hiện hành, chúng tôi có thể sử dụng
                  dữ liệu cá nhân của bạn cho các cơ sở pháp lý đã được công
                  nhận, bao gồm tuân thủ các nghĩa vụ pháp lý của chúng tôi, để
                  thực hiện hợp đồng của chúng tôi với bạn, để đạt được lợi ích
                  hợp pháp và lý do của chúng tôi để sử dụng dữ liệu đó cao hơn
                  bất kỳ phương hại nào đến quyền bảo vệ dữ liệu của bạn hoặc
                  khi cần thiết liên quan với một yêu cầu pháp lý.
                  <br />
                  <br />
                  - Các bên bất hợp pháp (tin tặc) có thể chặn hoặc truy cập
                  trái phép dữ liệu cá nhân có trên trang web, nền tảng của
                  chúng tôi có thể hoạt động không chính xác hoặc không hoạt
                  động như dự kiến, hoặc có người có thể truy cập, lạm dụng hoặc
                  sử dụng sai trái thông tin mà không phải lỗi của chúng tôi.
                  Tuy nhiên chúng tôi sẽ triển khai các biện pháp bảo mật hợp lý
                  để bảo vệ dữ liệu cá nhân của bạn theo quy định của các quy
                  định của pháp luật về bảo vệ bí mật thông tin cá nhân; tuy
                  nhiên không thể đảm bảo sự bảo mật tuyệt đối chẳng hạn như
                  trường hợp tiết lộ trái phép phát sinh từ hoạt động tin tặc vì
                  ý đồ xấu hoặc hành vi tấn công tinh vi bởi kẻ xấu mà không
                  phải lỗi của chúng tôi.
                  <br />
                  <br />
                  - Bất kể quy định nào được quy định tại đây, tất cả người dùng
                  và bên thư ba/người được phép truy cập dữ liệu cá nhân từ
                  ADDDA, (i) không được cho phép sử dụng các dữ liệu cá nhân trừ
                  khi có lý do chính đáng cần thiết để phản hồi và để thực hiện
                  việc trả lời, xử lý, giải quyết hoặc hoàn thành các giao dịch
                  mà không có sự cho phép trước bằng văn bản của người dùng và
                  ADDDA; (ii) phải ngưng việc liên lạc với Người mua sử dụng các
                  thông tin này bên ngoài nền tảng ADDDA; (iii) không được cho
                  phép tiết lộ các dữ liệu cá nhân đến bất cứ bên thứ ba không
                  được phép nào mà không có sự cho phép trước bằng văn bản của
                  người dùng đó và ADDDA; (iv) phải thực hiện các biện pháp an
                  ninh thích hợp để bảo vệ từng dữ liệu cá nhân người dùng của
                  ADDDA mà họ đang sở hữu, chỉ lưu giữ dữ liệu này chừng nào vẫn
                  còn cần thiết cho các mục đích ở trên và phù hợp với quy định
                  của pháp luật bảo vệ dữ liệu cá nhân, và xóa hoặc hoàn trả các
                  dữ liệu này cho ADDDA theo yêu cầu từ ADDDA hoặc trong thời
                  gian sớm nhất có thể khi hoàn thành giao dịch; và (v) thông
                  báo cho ADDDA tại contact@ADDDA.vn trong trường hợp có khả
                  năng vi phạm dữ liệu hoặc mất dữ liệu khác của người dùng này.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  6. Cách ADDDA lưu trữ, bảo vệ thông tin người dùng và những
                  cảnh báo rủi ro
                </p>
                <p>
                  - Chúng tôi thực hiện các biện pháp bảo mật khác nhau và luôn
                  nỗ lực để đảm bảo sự an toàn của dữ liệu cá nhân của bạn trên
                  các hệ thống của chúng tôi. Dữ liệu cá nhân của người dùng
                  được lưu trữ đằng sau các mạng bảo mật và chỉ có thể được truy
                  cập bởi một số nhân viên có quyền truy cập đặc biệt đến các hệ
                  thống của chúng tôi. Tuy nhiên, chắc chắn không thể có sự đảm
                  bảo an ninh tuyệt đối.
                  <br />
                  <br />
                  - Chúng tôi sẽ duy trì dữ liệu cá nhân tuân theo các quy định
                  của pháp luật về bảo vệ bí mật thông tin cá nhân và/hoặc các
                  điều luật hiện hành khác. Có nghĩa là, chúng tôi sẽ hủy hoặc
                  xóa thông tin nhận dạng ra khỏi dữ liệu cá nhân của bạn khi
                  chúng tôi có lý do hợp lý để xác định rằng (i) việc lưu giữ dữ
                  liệu cá nhân đó không còn phục vụ mục đích thu thập dữ liệu cá
                  nhân đó nữa; (ii) việc lưu giữ không còn cần thiết cho bất kỳ
                  mục đích hợp pháp hay mục đích kinh doanh nào và (iii) không
                  còn các lợi ích hợp pháp nào khác để tiếp tục lưu giữ các dữ
                  liệu cá nhân này. Nếu bạn ngưng sử dụng Nền tảng của chúng
                  tôi, hoặc quyền của bạn được sử dụng Nền tảng và/hoặc Các Dịch
                  Vụ bị chấm dứt hoặc hủy bỏ, chúng tôi có thể tiếp tục lưu, sử
                  dụng và/hoặc tiết lộ dữ liệu cá nhân của bạn tuân theo Chính
                  sách bảo mật này và các nghĩa vụ của chúng tôi theo các quy
                  định của pháp luật về bảo vệ bí mật thông tin cá nhân. Tùy
                  thuộc vào quy định của pháp luật, chúng tôi có thể tiêu hủy dữ
                  liệu cá nhân của bạn một cách an toàn mà không cần thông báo
                  trước cho bạn. Trong trường hợp máy chủ lưu trữ thông tin bị
                  hacker tấn công dẫn đến mất mát dữ liệu cá nhân thành viên,
                  ADDDA sẽ có trách nhiệm thông báo vụ việc cho cơ quan chức
                  năng điều tra xử lý kịp thời và thông báo cho thành viên được
                  biết.
                  <br />
                  <br />
                  - ADDDA yêu cầu các cá nhân khi đăng ký là thành viên, phải
                  cung cấp đầy đủ thông tin cá nhân có liên quan như: Họ và tên,
                  địa chỉ liên lạc, email, chứng minh nhân dân/căn cước công
                  dân, giấy phép lái xe ô tô, điện thoại, số tài khoản, số thẻ
                  thanh toán …., và chịu trách nhiệm về tính pháp lý của những
                  thông tin trên. ADDDA không chịu trách nhiệm cũng như không
                  giải quyết mọi khiếu nại có liên quan đến quyền lợi của thành
                  viên đó nếu xét thấy tất cả thông tin cá nhân của thành viên
                  đó cung cấp khi đăng ký ban đầu là không chính xác.
                  <br />
                  <br />
                  - Thời gian lưu trữ thông tin: Dữ liệu cá nhân của thành viên
                  sẽ được lưu trữ cho đến khi có yêu cầu hủy bỏ hoặc tự thành
                  viên đăng nhập và thực hiện hủy bỏ, còn lại trong mọi trường
                  hợp thông tin cá nhân thành viên sẽ được bảo mật trên máy chủ
                  của ADDDA.vn.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  7. Phương tiện và công cụ để người dùng tiếp cận, chỉnh sửa và
                  hủy bỏ dữ liệu cá nhân của mình
                </p>
                <p>
                  - Người dùng có quyền tự kiểm tra, cập nhật, điều chỉnh hoặc
                  hủy bỏ thông tin cá nhân của mình bằng cách đăng nhập vào tài
                  khoản và chỉnh sửa thông tin cá nhân hoặc yêu cầu ADDDA thực
                  hiện việc này.
                  <br />
                  <br />
                  - Thành viên có quyền gửi khiếu nại về việc lộ thông tin các
                  nhân cho bên thứ 3 đến ADDDA. Khi tiếp nhận những phản hồi
                  này, ADDDA sẽ xác nhận lại thông tin, phải có trách nhiệm trả
                  lời lý do và hướng dẫn thành viên khôi phục và bảo mật lại
                  thông tin.
                  <br />
                  <br />
                  - Email: contact@ADDDA.vn
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  8. Thu thập các dữ liệu khác
                </p>
                <p>
                  - Như với hầu hết các trang web và các ứng dụng di động khác,
                  thiết bị của bạn gửi thông tin có thể gồm có dữ liệu về bạn,
                  được một máy chủ web ghi lại khi bạn sử dụng Nền tảng của
                  chúng tôi. Thông tin này thông thường bao gồm nhưng không giới
                  hạn địa chỉ IP, hệ điều hành của máy tính/thiết bị di động,
                  loại trình duyệt, loại thiết bị di động, các đặc điểm của
                  thiết bị di động, mã định danh thiết bị thống nhất (UDID) hoặc
                  mã định danh thiết bị di động (MEID) của thiết bị di động của
                  bạn, địa chỉ tham chiếu của Trang Web (nếu có), các trang mà
                  bạn đã truy cập đến trên trang web hoặc ứng dụng di động của
                  chúng tôi và thời gian truy cập và đôi khi là "cookie" (có thể
                  vô hiệu hóa bằng cách sử dụng tùy chọn trình duyệt của bạn) để
                  giúp trang web ghi nhớ lần truy cập cuối cùng của bạn. Nếu bạn
                  đăng nhập, thông tin này được liên kết với tài khoản cá nhân
                  của bạn. Thông tin này cũng được đưa vào các số liệu thống kê
                  ẩn danh để giúp chúng tôi hiểu được khách truy cập sử dụng
                  Trang Web của chúng tôi như thế nào.
                  <br />
                  <br />
                  - Các ứng dụng di động của chúng tôi có thể thu thập thông tin
                  chính xác về địa chỉ của thiết bị di động của bạn sử dụng các
                  công nghệ như GPS, Wi-Fi,…. Chúng tôi thu thập, sử dụng, công
                  bố và/hoặc xử lý các thông tin này cho một hoặc nhiều mục đích
                  bao gồm nhưng không giới hạn các dịch vụ được cung cấp dựa
                  trên vị trí mà bạn yêu cầu hoặc chuyển các nội dung có liên
                  quan đến bạn dựa trên vị trí của bạn hoặc cho phép bạn chia sẻ
                  vị trí của bạn cho các Người sử dụng khác như là một phần của
                  các Dịch vụ được cung cấp bởi các ứng dụng di động của chúng
                  tôi. Đối với phần lớn các thiết bị di động, bạn có thể rút lại
                  sự cho phép để chúng tôi được thu thập các thông tin này dựa
                  trên vị trí của bạn thông qua các cài đặt trên thiết bị. Nếu
                  bạn có câu hỏi nào về cách thức vô hiệu hóa các dịch vụ vị trí
                  trên thiết bị di động của bạn, vui lòng liên hệ với các nhà
                  cung cấp dịch vụ thiết bị di động hoặc nhà sản xuất thiết bị
                  của bạn.
                  <br />
                  <br />
                  - Như khi bạn xem các trang trên trang web hoặc ứng dụng di
                  động của chúng tôi, khi bạn xem các nội dung và quảng cáo và
                  truy cập vào phần mềm khác trên Nền tảng của chúng tôi hoặc
                  thông qua Dịch vụ, phần lớn các thông tin tương tự được gửi
                  đến cho chúng tôi (bao gồm nhưng không giới hạn, địa chỉ IP,
                  hệ điều hành, v.v..); nhưng, thay vì các số lượt xem trang,
                  thiết bị của bạn gửi đến chúng tôi các thông tin về nội dung,
                  quảng cáo được xem và/hoặc phần mềm được cài đặt bởi các Dịch
                  vụ và Nền tảng và thời điểm.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  4. Những người hoặc tổ chức có thể được tiếp cận với thông tin
                  <br />
                </p>
                <p>
                  - ADDDA sẽ không tiết lộ thông tin của khách hàng cho các bên
                  thứ ba vì mục đích kinh doanh hoặc tiếp thị độc lập của riêng
                  họ mà không có sự đồng ý của bạn. Tuy nhiên, ADDDA có thể tiết
                  lộ thông tin của khách hàng cho các đối tượng sau:
                  <br />
                  <br />
                  - Các cơ quan nhà nước có thẩm quyền: ADDDA sẽ cung cấp thông
                  tin cá nhân của khách hàng theo yêu cầu của các cơ quan này.
                  <br />
                  - Ban quản trị ADDDA, Bộ phận kinh doanh, Bộ phận Chăm sóc
                  khách hàng và các cá nhân được phân công thực hiện các công
                  việc có liên quan: Việc tiếp cận thông tin của các đối tượng
                  này nhằm cung cấp dịch vụ cũng như hỗ trợ khách hàng trong quá
                  trình sử dụng các dịch vụ được cung cấp bởi ADDDA.
                  <br />
                  - Các bên khác khi có sự đồng ý hoặc theo hướng dẫn của khách
                  hàng: Ngoài các tiết lộ như được mô tả trong Chính sách bảo
                  mật thông tin cá nhân này, ADDDA có thể chia sẻ thông tin cá
                  nhân của khách hàng với các bên thứ ba khi khách hàng đồng ý
                  hoặc yêu cầu việc chia sẻ như vậy.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>9. Cookies</p>
                <p>
                  - Đôi khi chúng tôi hoặc các nhà cung cấp dịch vụ được cho
                  phép và các đối tác quảng cáo của chúng tôi có thể sử dụng
                  "cookie" hoặc các tính năng khác để cho phép chúng tôi hoặc
                  các bên thứ ba thu thập hoặc chia sẻ thông tin liên quan đến
                  việc sử dụng của bạn đối với Dịch vụ hoặc Nền tảng của chúng
                  tôi. Các tính năng này sẽ giúp chúng tôi cải thiện Nền tảng và
                  Các Dịch Vụ chúng tôi cung cấp, giúp chúng tôi đề xuất các
                  dịch vụ và tính năng mới, và/hoặc cho phép chúng tôi và các
                  đối tác quảng cáo của chúng tôi cung cấp các nội dung có liên
                  quan hơn đến bạn. "Cookie" là các mã danh định được lưu trữ
                  trên máy tính hoặc thiết bị di động của bạn lưu trữ các dữ
                  liệu về máy tính hoặc thiết bị, bằng cách nào và khi nào Các
                  Dịch Vụ hoặc Nền tảng được sử dụng hay truy cập, bởi bao nhiêu
                  người và để theo dõi những hoạt động trong Các Nền tảng của
                  chúng tôi. Chúng tôi có thể liên kết thông tin cookie với dữ
                  liệu cá nhân. Cookie cũng liên kết với thông tin về những nội
                  dung bạn đã chọn để mua sắm và các trang web mà bạn đã xem.
                  Thông tin này được sử dụng để theo dõi giỏ hàng, để chuyển tải
                  nội dung phù hợp với sở thích của bạn, để cho phép các đối tác
                  cung cấp dịch vụ quảng cáo cung cấp dịch vụ quảng cáo trên các
                  trang thông qua mạng Internet và để thực hiện phân tích dữ
                  liệu và hoặc theo dõi việc sử dụng Dịch vụ.
                  <br />
                  <br />
                  - Bạn có thể từ chối sử dụng cookie bằng cách chọn các thiết
                  lập thích hợp trên trình duyệt hoặc thiết bị của bạn. Tuy
                  nhiên, vui lòng lưu ý rằng nếu bạn thực hiện thao tác này bạn
                  có thể không sử dụng được các chức năng đầy đủ của Nền tảng
                  hoặc Các Dịch Vụ của chúng tôi.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  10. Thông tin về trẻ em
                </p>
                <p>
                  - Các Dịch Vụ này không dành cho trẻ em dưới 18 tuổi. Chúng
                  tôi không cố tình thu thập hay lưu giữ bất kỳ dữ liệu cá nhân
                  hay thông tin không nhận dạng cá nhân nào của bất kỳ ai dưới
                  18 tuổi, bất kỳ phần nào của Nền tảng của chúng tôi hoặc Các
                  Dịch Vụ khác cũng không dành cho trẻ em dưới 18 tuổi. Bố/mẹ
                  hoặc người giám hộ của trẻ em dưới 18 tuổi vui lòng giám sát
                  và đảm bảo thông tin cá nhân của trẻ dưới 18 tuổi mà mình đang
                  giám hộ không đăng tải thông tin cá nhân cho ADDDA. Trong
                  trường hợp thông tin cá nhân của của trẻ em dưới 18 tuổi do
                  quý phụ huynh giám hộ được cung cấp cho ADDDA, Bố/mẹ hoặc
                  người giám hộ theo đồng ý với việc xử lý thông tin của trẻ em
                  dưới 18 tuổi có liên quan, và đồng ý chịu sự điều chỉnh của
                  Chính Sách này thay mặt cho người được giám hộ. Chúng tôi sẽ
                  khóa bất kỳ tài khoản nào được sử dụng bởi đối tượng trẻ em
                  như vậy và sẽ gỡ và/hoặc xóa bất kỳ dữ liệu cá nhân nào mà
                  chúng tôi cho là đã được gửi bởi bất kỳ trẻ em nào dưới 18
                  tuổi.
                  <br />
                </p>
                <p style={{ fontWeight: 'bold', fontSize: 19 }}>
                  11. Loại trừ trách nhiệm về nghĩa vụ bảo mật và các trang web
                  bên thứ ba
                </p>
                <p>
                  -{' '}
                  <a style={{ fontWeight: 'bold' }}>
                    Chúng Tôi Không Đảm Bảo Tính Bảo Mật Đối Với Dữ Liệu Cá Nhân
                    Và/Hoặc Thông Tin Khác Mà Bạn Cung Cấp Trên Các Trang Web
                    Của Bên Thứ Ba
                  </a>
                  . Chúng tôi thực hiện các biện pháp bảo mật khác nhau để duy
                  trì sự an toàn của dữ liệu cá nhân của bạn mà chúng tôi lưu
                  giữ hoặc kiểm soát. Dữ liệu cá nhân của bạn được lưu đằng sau
                  các mạng bảo mật và chỉ có thể được truy cập bởi một số cá
                  nhân giới hạn có quyền truy cập đặc biệt đến các hệ thống của
                  chúng tôi, và đã được yêu cầu bảo mật dữ liệu cá nhân đó. Khi
                  bạn đặt hàng hoặc truy cập dữ liệu cá nhân của bạn, chúng tôi
                  đề nghị sử dụng một máy chủ bảo mật. Tất cả dữ liệu cá nhân
                  hoặc thông tin cá nhân bạn cung cấp sẽ được mã hóa vào các cơ
                  sở dữ liệu của chúng tôi để chỉ được truy cập như mô tả bên
                  trên.
                  <br />
                  <br />
                  - Nhằm cung cấp cho bạn giá trị gia tăng, chúng tôi có thể
                  chọn các trang web hoặc ứng dụng hoặc dịch vụ của bên thứ ba
                  khác nhau để liên kết, và đóng khung bên trong Nền tảng. Chúng
                  tôi cũng có thể tham gia các quan hệ cùng tiếp thị và các quan
                  hệ khác để cung cấp dịch vụ thương mại điện tử và các dịch vụ
                  và tính năng khác cho khách truy cập. Những trang được liên
                  kết này có các chính sách về quyền riêng tư cũng như các biện
                  pháp bảo mật riêng và độc lập. Ngay cả khi bên thứ ba đó có
                  liên kết với chúng tôi, chúng tôi cũng không kiểm soát các
                  trang web/ứng dụng/dịch vụ được liên kết này, mỗi trang đó có
                  các phương pháp bảo vệ quyền riêng tư và thu thập dữ liệu
                  riêng biệt, độc lập với chúng tôi. Dữ liệu thu thập bởi các
                  đối tác cùng tiếp thị của chúng tôi hoặc các trang web/ứng
                  dụng/dịch vụ của bên thứ ba (ngay cả khi được cung cấp trên
                  hoặc thông qua Nền tảng của chúng tôi) có thể không được chúng
                  tôi tiếp cận và/hoặc lưu giữ.
                  <br />
                  <br />
                  - Do đó chúng tôi không chịu trách nhiệm hay trách nhiệm pháp
                  lý đối với nội dung, các biện pháp bảo mật (hoặc sự thiếu biện
                  pháp bảo mật) và các hoạt động của các trang web/ứng dụng/dịch
                  vụ được liên kết này. Những trang web/ứng dụng/dịch vụ được
                  liên kết này chỉ vì sự thuận tiện cho bạn và do đó bạn tự chịu
                  trách nhiệm khi truy cập chúng. Tuy nhiên, chúng tôi tìm cách
                  bảo vệ tính toàn vẹn của Nền tảng của chúng tôi và các liên
                  kết được đặt trên từng trang web đó và do đó chúng tôi hoan
                  nghênh ý kiến phản hồi về các trang web được liên kết này (bao
                  gồm nếu một trang web cụ thể không hoạt động).
                  <br />
                </p>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default ChinhSach;
